.aboutMain {
  display: flex;
  flex-direction: column;

  &__collapses {
    .collapse:not(:first-child) {
      margin-top: 30px;
    }
    margin: 38px 0;
    padding: 0 208px;
  }
}
@media screen and (max-width: 1024px) {
  .aboutMain {
    &__collapses {
      padding: 0 100px;
    }
  }
}

@media screen and (max-width: 767px) {
  .aboutMain {
    &__collapses {
      .collapse:not(:first-child) {
        margin-top: 20px;
      }

      margin: 19px 0;
      padding: 0 20px;
    }
  }
}
